import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			filterByList: [{ text: '全部', selected: true, filterby: 'all' }, { text: '口碑', selected: false, filterby: 'public' }, { text: '热门', selected: false, filterby: 'hot' }],
			filterby: 'all',
			page: 1,
			size: 6,
			loadingText: '正在加载中...',
			goodsList: []
		};
	},
	onLoad(option) {
		// console.log(option.name)
		//动态修改nav title
		uni.setNavigationBarTitle({
			title: option.name
		});
		this.initData();
	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.init();
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReachBottom() {
		this.page++;
		this.initData();
	},
	methods: {
		init() {
			this.page = 1;
			this.loadingText = '加载中...';
			this.goodsList = [];
			this.initData();
		},
		initData() {
			this.request({
				url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
				success: res => {
					// console.log(res);
					// this.goodsList = res.data;
					if (res.data.length > 0) {
						res.data.forEach(item => {
							this.goodsList.push(item);
						});
					} else {
						this.loadingText = '到底了';
					}
				}
			});
		},
		handleSelect(index) {
			this.filterByList[index].selected = true;
			// 其他的selected false
			for (let i = 0; i < this.filterByList.length; i++) {
				if (i != index) {
					this.filterByList[i].selected = false;
				}
			}

			//请求数据
			this.filterby = this.filterByList[index].filterby;
			this.init();
		},
		handleGoods(goods) {
			// 页面跳转 商品详情
			uni.navigateTo({
				url: './goods?goodsInfo=' + JSON.stringify(goods)
			});
		}
	}
};
